export default {
	name: "mine",
	data() {
		return {
			show: false,
			showInfo: false,
			nickname: '',
		}
	},
	mounted: function() {
		let userInfo = uni.getStorageSync('loginInfo')
		if (userInfo !== null && userInfo != '') {
			this.nickname = userInfo.nickName
			this.showInfo = true
		}
	},
	methods: {
		toLogin: function() {
			uni.navigateTo({
				url: '/pages/component/login/index'
			});
		}
	}
}
